import React from "react";
import { BrowserRouter as Router, Routes, Route, Link, useLocation } from "react-router-dom";
import FlowEditor from "./page/FlowEditor";
import QuestionForm from "./page/QuestionForm";
import Edit from "./page/Editor";
import './App.css';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPaperPlane,faClipboard, faClipboardCheck } from "@fortawesome/free-solid-svg-icons";
import Test from './page/Test';
import Test2 from './page/test2';
import Test3 from './page/TemplateEditor';


// ประกาศ TopNav แยกออกไปข้างนอกฟังก์ชัน App
function TopNav() {
  const location = useLocation();

  return (
    <nav className="topnav">
      <Link
        to="/"
        className={`nav-link ${location.pathname === "/" ? "active" : ""}`}
      >
        <FontAwesomeIcon icon={ faClipboardCheck} /> Flow Editor (ProUser)
      </Link>
      <Link
        to="/form"
        className={`nav-link ${location.pathname === "/form" ? "active" : ""}`}
      >
        <FontAwesomeIcon icon={ faPaperPlane} /> แบบฟอร์มคำถาม (ProUser)
      </Link>

      <Link
        to="/test3"
        className={`nav-link ${location.pathname === "/test3" ? "active" : ""}`}
      >
        <FontAwesomeIcon icon={ faClipboard} /> TemplatePlacehoder (ProUser)
      </Link>

      <Link
        to="/formad"
        className={`nav-link ${location.pathname === "/formad" ? "active" : ""}`}
      >
        <FontAwesomeIcon icon={ faClipboard} /> คำถาม (User)
      </Link>
      {/* <Link
        to="/test"
        className={`nav-link ${location.pathname === "/test" ? "active" : ""}`}
      >
        <FontAwesomeIcon icon={ faClipboard} /> Test อันตรายนะ!!! (User)
      </Link> */}
      <Link
        to="/test2"
        className={`nav-link ${location.pathname === "/test2" ? "active" : ""}`}
      >
        <FontAwesomeIcon icon={ faClipboard} /> Test2 ช่วงเวลาดีๆของเธอ (User)
      </Link>

    </nav>
  );
}

function App() {
  return (
    <Router>
      {/* เรียกใช้ TopNav ใน Router */}
      <TopNav />

      <Routes>
        <Route path="/" element={<FlowEditor />} />
        <Route path="/form" element={<QuestionForm />} />
        <Route path="/formad" element={<Edit />} />
        <Route path="/test" element={<Test />} />
        <Route path="/test2" element={<Test2 />} />
        <Route path="/test3" element={<Test3/>}/>
      </Routes>
    </Router>
  );
}

export default App;
