import React, { useCallback, useEffect, useState } from "react";
import ReactFlow, {
  MiniMap,
  Controls,
  Background,
  addEdge,
  applyNodeChanges,
  applyEdgeChanges,
  MarkerType,
  onNodesDelete,
  onEdgesDelete,
} from "reactflow";
import "reactflow/dist/style.css";
import { fetchQuestions, saveQuestions } from "../api";
import './FlowEdit.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { fas, faPencil, faPen, faQuestion, faPlus } from '@fortawesome/free-solid-svg-icons';

export default function FlowEditor() {
  const [nodes, setNodes] = useState([]);
  const [edges, setEdges] = useState([]);
  const [selectedNodeId, setSelectedNodeId] = useState(null);
  const [questionsJson, setQuestionsJson] = useState([]);
  const [selectedNode, setSelectedNode] = useState(null);
  


  useEffect(() => {
    const json = convertFlowToQuestions(nodes, edges);
    setQuestionsJson(json);
  }, [nodes, edges]);

  useEffect(() => {
  fetchQuestions()
    .then((data) => {
      if (!data || !Array.isArray(data.nodes)) {
        console.error("Invalid data format:", data);
        setNodes([]);
        setEdges([]);
        return;
      }

      const initialNodes = data.nodes.map((q, i) => ({
        id: q.id,
        data: { label: q.question, type: q.type || "text" },
        position: q.position ?? { x: 100 + i * 150, y: 100 }, // ✅ fallback ถ้าไม่มี
      }));

      const initialEdges = [];

      for (const q of data.nodes) {
        if (q.type === "dropdown" && q.options) {
          q.options.forEach((opt) => {
            if (opt.next) {
              initialEdges.push({
                id: `${q.id}->${opt.next}`,
                source: q.id,
                target: opt.next,
                label: opt.label,
              });
            }
          });
        } else if (q.next) {
          initialEdges.push({
            id: `${q.id}->${q.next}`,
            source: q.id,
            target: q.next,
            label: "",
          });
        }
      }

      setNodes(initialNodes);
      setEdges(initialEdges);
    })
    .catch((err) => {
      console.error("Failed to fetch questions:", err);
      setNodes([]);
      setEdges([]);
    });
}, []);

  

  const onConnect = useCallback(
    (connection) => setEdges((eds) => addEdge(connection, eds)),
    []
  );

  const onNodesChange = useCallback((changes) => {
    setNodes((nds) => applyNodeChanges(changes, nds));
  }, []);

  const onEdgesChange = useCallback((changes) => {
    setEdges((eds) => applyEdgeChanges(changes, eds));
  }, []);

  const onNodeClick = useCallback((event, node) => {
    setSelectedNode(node);
    setSelectedNodeId(node.id); // ✅ เพิ่มบรรทัดนี้
  }, []);



const handleDeleteSelectedNode = () => {
  if (!selectedNode) return;

  setNodes((nds) => nds.filter((n) => n.id !== selectedNode.id));
  setEdges((eds) =>
    eds.filter((e) => e.source !== selectedNode.id && e.target !== selectedNode.id)
  );

  setSelectedNode(null);
};

  // ฟังก์ชันแก้ไข label คำถามและ type ของ node
const updateSelectedNode = (key, value) => {
  setNodes((nds) => {
    const newNodes = nds.map((n) => {
      if (n.id === selectedNodeId) {
        return {
          ...n,
          data: {
            ...n.data,
            [key]: value,
          },
        };
      }
      return n;
    });

    // อัปเดต selectedNode ด้วย (ถ้ามี)
    const updatedNode = newNodes.find((n) => n.id === selectedNodeId);
    setSelectedNode(updatedNode);

    return newNodes;
  });
};


  const handleNodesDelete = useCallback(
  (deletedNodes) => {
    setNodes((nds) =>
      nds.filter((node) => !deletedNodes.find((d) => d.id === node.id))
    );
    setEdges((eds) =>
      eds.filter((edge) => !deletedNodes.find((d) => d.id === edge.source || d.id === edge.target))
    );
  },
  []
);

const handleEdgesDelete = useCallback(
  (deletedEdges) => {
    setEdges((eds) =>
      eds.filter((edge) => !deletedEdges.find((d) => d.id === edge.id))
    );
  },
  []
);

const addNode = () => {
  const newId = `q${nodes.length + 1}`;
  const newNode = {
    id: newId,
    type: 'default',
    position: { x: 100 + nodes.length * 50, y: 100 + nodes.length * 50 },
    data: { label: `คำถามใหม่ (${newId})`, type: 'text' }
  };
  setNodes((nds) => [...nds, newNode]);
};


  // ฟังก์ชันแก้ไข label ของ edge
  const updateEdgeLabel = (edgeId, value) => {
    setEdges((eds) =>
      eds.map((e) => (e.id === edgeId ? { ...e, label: value } : e))
    );
  };

  // แปลง flow เป็น JSON สำหรับบันทึก
    const convertFlowToQuestions = (nodes, edges) => {
      return nodes.map((node) => {
        const connectedEdges = edges.filter((e) => e.source === node.id);

        const base = {
          id: node.id,
          question: node.data.label,
          type: node.data.type || "text",
          position: node.position, // ✅ บันทึกตำแหน่งด้วย
        };

        if (connectedEdges.length === 0) {
          return { ...base, next: null };
        }

        if (connectedEdges.length === 1) {
          return { ...base, next: connectedEdges[0].target };
        }

        return {
          ...base,
          type: "dropdown",
          options: connectedEdges.map((edge) => ({
            label: edge.label || "เลือก",
            next: edge.target,
          })),
        };
      });
    };


  const handleExport = () => {
    const json = convertFlowToQuestions(nodes, edges);
    console.log("📤 Exported JSON:", json);
    saveQuestions({ title: "ฟลโลว์จากแผนผัง", nodes: json });
    alert("✅ Flow ถูกบันทึกเป็น JSON แล้ว!");
  };

  return (
    <div style={{ display: "flex", height: "90vh", border: "1px solid #aaa" }}>
      <div style={{ flex: 1 }}>
        <ReactFlow
            nodes={nodes}
            edges={edges}
            onNodesChange={onNodesChange}
            onEdgesChange={onEdgesChange}
            onConnect={onConnect}
            onNodesDelete={handleNodesDelete}
            onEdgesDelete={handleEdgesDelete}
            fitView
            onNodeClick={onNodeClick}
            style={{
              background: 'linear-gradient(135deg, #f3e8ff, #fce7f3, #dbeafe)',
            }}
              defaultEdgeOptions={{
                animated: false,
                style: {
                  stroke: '#ff9900ff',
                  strokeWidth: 3,
                },
                markerEnd: {
                  type: MarkerType.ArrowClosed, // ใช้ enum MarkerType
                  width: 10,
                  height: 10,
                  color: '#ff3300ff',
                },
              }}
          >
            <MiniMap 
            />
            <Controls 
            />
            <Background 
            />
          </ReactFlow>
          </div>
          <div className="edit-panel">
            <h4><FontAwesomeIcon icon={faPen} /> แก้ไขคำถาม</h4>
            {!selectedNode ? (
              <p className="no-node-msg">คลิกที่ node เพื่อแก้ไขข้อมูล</p>
            ) : (
              <>
                <div className="form-group">
                  <label>ID: {selectedNode.id}</label>
                </div>

                <div className="form-group">
                  <label><FontAwesomeIcon icon={faQuestion} /> คำถาม:</label>
                  <input
                    type="text"
                    value={selectedNode.data.label}
                    onChange={(e) => updateSelectedNode("label", e.target.value)}
                  />
                </div>

                <div className="form-group">
                  <label><FontAwesomeIcon icon={faPen} /> ประเภทคำถาม:</label>
                  <select
                    value={selectedNode.data.type || "text"}
                    onChange={(e) => updateSelectedNode("type", e.target.value)}
                  >
                    <option value="text">Text</option>
                    <option value="dropdown">Dropdown</option>
                    <option value="date">Date</option>
                  </select>
                </div>

                {selectedNode.data.type === "dropdown" && (
                  <div className="dropdown-options">
                    <h5>แก้ไขตัวเลือก Dropdown</h5>
                    {edges
                      .filter((e) => e.source === selectedNode.id)
                      .map((edge) => (
                        <div key={edge.id} className="dropdown-item">
                          <label>ตัวเลือก label:</label>
                          <input
                            type="text"
                            value={edge.label || ""}
                            onChange={(e) => updateEdgeLabel(edge.id, e.target.value)}
                          />
                          <small>ไปยัง: {edge.target}</small>
                        </div>
                      ))}
                    <small className="hint-text">* แก้ไขตัวเลือกด้วยการลากเส้นเชื่อมต่อใหม่</small>
                  </div>
                )}
              </>
            )}

            <div className="button-group">
              <button className="btn-primary" onClick={handleExport}>
                 บันทึก Flow
              </button>
              {selectedNode && (
                <button className="btn-danger" onClick={handleDeleteSelectedNode}>
                   ลบ Node ที่เลือก ({selectedNode.id})
                </button>
              )}
              <button className="btn-add" onClick={addNode}>
                <FontAwesomeIcon icon={faPlus} /> เพิ่มคำถาม (Node)
              </button>

            </div>
          </div>
    </div>
  );
}