import React, { useState, useEffect } from "react";
import axios from "axios";
import "./SectionFillerManager.css";

function SectionFillerManager() {
  const [isOpen, setIsOpen] = useState(false);
  const [groupName, setGroupName] = useState("");
  const [questions, setQuestions] = useState([]);
  const [selectedQuestionIds, setSelectedQuestionIds] = useState([]);
  const [editingGroup, setEditingGroup] = useState(null); // null = กำลังสร้างใหม่
  const [groupList, setGroupList] = useState([]);

  useEffect(() => {
    if (isOpen) {
      axios.get("http://10.7.0.111:8002/api/lease_questions")
        .then((res) => {
          console.log("ได้คำถามจาก backend:", res.data);
          setQuestions(res.data);
        })
        .catch((err) => console.error("Error fetching questions:", err));
    }
  }, [isOpen]);

  const handleCheckboxChange = (id) => {
    setSelectedQuestionIds(prev =>
      prev.includes(id) ? prev.filter(qid => qid !== id) : [...prev, id]
    );
  };

    const handleSubmit = () => {
    const payload = {
        groupName,
        questionIds: selectedQuestionIds
    };

    const method = editingGroup ? 'put' : 'post';
    const url = editingGroup
        ? `http://10.7.0.111:8002/api/section_groups/${editingGroup}`
        : "http://10.7.0.111:8002/api/section_groups";

    axios[method](url, payload)
        .then(res => {
        alert("บันทึกสำเร็จ");
        setIsOpen(false);
        setGroupName("");
        setSelectedQuestionIds([]);
        setEditingGroup(null);
        })
        .catch(err => {
        console.error("เกิดข้อผิดพลาดในการบันทึก:", err);
        });


    console.log("Submitting Section Group:", payload);
  };

    const handleEdit = (group) => {
    setGroupName(group.groupName);
    setSelectedQuestionIds(group.questionIds);
    setEditingGroup(group.groupName); // ใช้เป็น key สำหรับ PUT
    setIsOpen(true);
    };

    const handleDelete = (groupName) => {
    if (window.confirm(`คุณแน่ใจว่าต้องการลบกลุ่ม "${groupName}"?`)) {
        axios.delete(`http://10.7.0.111:8002/api/section_groups/${groupName}`)
        .then(() => {
            alert("ลบสำเร็จ");
            setGroupList(groupList.filter(g => g.groupName !== groupName));
        })
        .catch(err => {
            console.error("ลบไม่สำเร็จ:", err);
            alert("เกิดข้อผิดพลาดในการลบ");
        });
    }
    };
  return (
    <div>
      <button onClick={() => setIsOpen(true)}>
        จัดการ Section Filler
      </button>
        <h3>รายการกลุ่ม Section ที่มี</h3>
        <ul className="group-list">
        {groupList.map((group) => (
            <li key={group.groupName}>
            <strong>{group.groupName}</strong> ({group.questionIds.length} ข้อ)
            <button onClick={() => handleEdit(group)}>แก้ไข</button>
            <button onClick={() => handleDelete(group.groupName)}>ลบ</button>
            </li>
        ))}
        </ul>

      {isOpen && (
        <div className="popup-overlay">
          <div className="popup-box">
            <h3>จัดกลุ่มคำถาม</h3>
            <label>ชื่อกลุ่ม:</label>
            <input
              type="text"
              value={groupName}
              onChange={(e) => setGroupName(e.target.value)}
              placeholder="เช่น Cover Sheet"
            />

            <div className="question-list">
              {questions.map(q => (
                <label key={q.id} className="question-item">
                  <input
                    type="checkbox"
                    checked={selectedQuestionIds.includes(q.id)}
                    onChange={() => handleCheckboxChange(q.id)}
                  />
                  {q.id} - {q.question}
                </label>
              ))}
            </div>

            <div className="popup-actions">
              <button onClick={handleSubmit}>บันทึก</button>
              <button onClick={() => setIsOpen(false)}>ยกเลิก</button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default SectionFillerManager;
