import { useEffect, useState } from "react";
import './QuestionEditor.css';
import axios from "axios";
import {
  DragDropContext,
  Droppable,
  Draggable,
} from "@hello-pangea/dnd";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faPlus,
  faClipboardCheck,
  faTrashCan,
  faBookJournalWhills,
  faSearch
} from "@fortawesome/free-solid-svg-icons";
import SectionFillerManager from "./SectionFillerManager";

export default function FlowEditor() {
  const [questions, setQuestions] = useState([]);
  const [title, setTitle] = useState("");
  const [searchTerm, setSearchTerm] = useState("");
  const [groupList, setGroupList] = useState([]);
  const [selectedGroup, setSelectedGroup] = useState(null);

  useEffect(() => {
    fetch("http://10.7.0.111:8000/questions")
      .then(res => res.json())
      .then(data => {
        setQuestions(data.nodes || []);
        setTitle(data.title || "");
      });
  }, []);

  const addQuestion = () => {
    setQuestions([
      ...questions,
      {
        id: `q${questions.length + 1}`,
        question: "",
        type: "text",
        next: null,
        options: []
      }
    ]);
  };

  const updateQuestion = (index, key, value) => {
    const updated = [...questions];
    updated[index][key] = value;
    setQuestions(updated);
  };

  const addDropdownOption = (index) => {
    const updated = [...questions];
    if (!updated[index].options) updated[index].options = [];
    updated[index].options.push({ label: "", next: "" });
    setQuestions(updated);
  };

  const updateDropdownOption = (qIndex, oIndex, key, value) => {
    const updated = [...questions];
    updated[qIndex].options[oIndex][key] = value;
    setQuestions(updated);
  };

  const removeDropdownOption = (qIdx, optIdx) => {
    const updated = [...questions];
    updated[qIdx].options.splice(optIdx, 1);
    setQuestions(updated);
  };

  const removeQuestion = (indexToRemove) => {
    const updated = [...questions];
    updated.splice(indexToRemove, 1);

    const reindexed = updated.map((q, i) => ({
      ...q,
      id: `q${i + 1}`
    }));

    const idMap = {};
    updated.forEach((old, i) => {
      idMap[old.id] = `q${i + 1}`;
    });

    const final = reindexed.map(q => {
      const updatedQ = { ...q };
      if (updatedQ.next && idMap[updatedQ.next]) {
        updatedQ.next = idMap[updatedQ.next];
      }
      if (updatedQ.type === "dropdown" && updatedQ.options) {
        updatedQ.options = updatedQ.options.map(opt => ({
          ...opt,
          next: idMap[opt.next] || ""
        }));
      }
      return updatedQ;
    });

    setQuestions(final);
  };

  const saveQuestions = () => {
    const payload = {
      title,
      nodes: questions
    };
    fetch("http://10.7.0.111:8000/questions", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(payload)
    }).then(res => {
      if (res.ok) alert("✅ บันทึกคำถามเรียบร้อยแล้ว");
      else alert("❌ บันทึกล้มเหลว");
    });
  };

  const handleDragEnd = (result) => {
    if (!result.destination) return;

    const newQuestions = Array.from(questions);
    const [moved] = newQuestions.splice(result.source.index, 1);
    newQuestions.splice(result.destination.index, 0, moved);

    setQuestions(newQuestions);
  };

  const filteredQuestions = questions.filter(q =>
  q.question.toLowerCase().includes(searchTerm.toLowerCase())
);

useEffect(() => {
  axios.get("http://10.7.0.111:8002/api/section_groups")
    .then(res => setGroupList(res.data))
    .catch(err => console.error("Error fetching groups:", err));
}, []);



return (
  <div className="question-editor-container">
    <h2 className="question-editor-title">
      <FontAwesomeIcon icon={faBookJournalWhills} /> สร้าง/แก้ไข Flow คำถาม
    </h2>
    <SectionFillerManager/>
    <div style={{ display: "flex", alignItems: "center", marginBottom: 16 }}>
      <FontAwesomeIcon icon={faSearch} style={{ marginRight: 8 }} />
      <input
        type="text"
        placeholder="ค้นหาคำถาม..."
        value={searchTerm}
        onChange={(e) => setSearchTerm(e.target.value)}
        style={{ padding: "8px", width: "100%", fontSize: "16px" }}
      />
      {searchTerm && (
        <button onClick={() => setSearchTerm("")} 
        style={{                                 
          color: "red",
          width: "40px",
          height: "40px",
          border: "none",
          borderRadius: "4px",
          backgroundColor: "red",
          marginLeft: "10px" 
          }}>
          <FontAwesomeIcon icon={faTrashCan} color="white" />
        </button>
      )}
    </div>

    <DragDropContext onDragEnd={handleDragEnd}>
      <Droppable droppableId="questions-list">
        {(provided) => (
          <div {...provided.droppableProps} ref={provided.innerRef}>
            {filteredQuestions.map((q, idx) => (
              <Draggable key={q.id} draggableId={q.id} index={idx}>
                {(provided) => (
                  <div
                    className="question-card"
                    ref={provided.innerRef}
                    {...provided.draggableProps}
                    {...provided.dragHandleProps}
                  >
                    {/* ...ส่วนแสดงคำถามเหมือนเดิม */}
                    <div>
                      <label><FontAwesomeIcon icon={faClipboardCheck} /> ID: {q.id}</label>
                    </div>
                    <input
                      value={q.question}
                      onChange={e => updateQuestion(idx, "question", e.target.value)}
                      placeholder="คำถาม"
                    />
                    <select
                      value={q.type}
                      onChange={e => updateQuestion(idx, "type", e.target.value)}
                    >
                      <option value="text">Text</option>
                      <option value="dropdown">Dropdown</option>
                      <option value="date">Date</option>
                    </select>

                    {q.type === "dropdown" && (
                      <div className="dropdown-options">
                        <strong><FontAwesomeIcon icon={faPlus} /> ตัวเลือก Dropdown:</strong>
                        {q.options?.map((opt, oIdx) => (
                          <div key={oIdx} className="dropdown-option">
                            <input
                              value={opt.label}
                              onChange={e =>
                                updateDropdownOption(idx, oIdx, "label", e.target.value)
                              }
                              placeholder="Yes, No, other"
                            />
                            <select
                              style={{ width: "50%" }}
                              value={opt.next || ""}
                              onChange={e =>
                                updateDropdownOption(idx, oIdx, "next", e.target.value)
                              }
                            >
                              <option value="">-- เลือกคำถามถัดไป --</option>
                              {questions.map((qItem) => (
                                <option key={qItem.id} value={qItem.id}>
                                  {qItem.id} — {qItem.question}
                                </option>
                              ))}
                            </select>
                            <button
                              onClick={() => removeDropdownOption(idx, oIdx)}
                              style={{
                                color: "red",
                                width: "40px",
                                height: "35px",
                                border: "none",
                                borderRadius: "6px",
                                backgroundColor: "red",
                              }}
                            >
                              <FontAwesomeIcon icon={faTrashCan} color="white" />
                            </button>
                          </div>
                        ))}
                        <button
                          style={{ marginLeft: "5px" }}
                          className="add-button-aa"
                          onClick={() => addDropdownOption(idx)}
                        >
                          <FontAwesomeIcon icon={faPlus} /> เพิ่มตัวเลือก
                        </button>
                      </div>
                    )}

                    <div style={{ marginTop: 10 }}>
                      <select
                        value={q.next || ""}
                        onChange={e => updateQuestion(idx, "next", e.target.value)}
                      >
                        <option value="">-- เลือกคำถามถัดไป --</option>
                        {questions.map(qItem => (
                          <option key={qItem.id} value={qItem.id}>
                            {qItem.id}: {qItem.question.slice(0, 40)}...
                          </option>
                        ))}
                      </select>
                    </div>

                    <button
                      onClick={() => removeQuestion(idx)}
                      className="btn-delete-q"
                    >
                      <FontAwesomeIcon icon={faTrashCan} /> ลบคำถาม : {q.id}
                    </button>
                  </div>
                )}
              </Draggable>
            ))}
            {provided.placeholder}
          </div>
        )}
      </Droppable>
    </DragDropContext>

    <div className="button-group">
      <button className="add-button" onClick={addQuestion}>
        <FontAwesomeIcon icon={faPlus} /> เพิ่มคำถาม
      </button>
      <button className="save-button" onClick={saveQuestions}>
        <FontAwesomeIcon icon={faClipboardCheck} /> บันทึก
      </button>
    </div>
  </div>
);
}
