import React, { useState } from 'react';
import './test2.css'
const DocumentTemplateManager = () => {
  const [jsonData, setJsonData] = useState('');
  const [parsedData, setParsedData] = useState(null);
  const [error, setError] = useState('');

  // ตัวอย่างข้อมูล
  const sampleData = `{
    "Answer 8": {
      "q1: ข้อตกลงของคุณควรมีหน้าปกที่มีหัวข้อ วันที่ สถานที่ ชื่อบริษัท และคู่สัญญาหรือไม่?": "ใช่",
      "q2: ข้อตกลงจะลงนามที่ไหน?": "บ้าน",
      "q3: ข้อตกลงจะลงนามในวันไหน?": "2025-08-29",
      "q4: ตามกฎหมายบริษัทไทย บริษัทจำกัดจำเป็นต้องมีผู้ถือหุ้นอย่างน้อยหนึ่งคน ผู้ถือหุ้นคนแรกชื่ออะไร?": "เร็ก",
      "q5: มีผู้ถือหุ้นอีกคนหรือไม่?": "ไม่ใช่",
      "q6: คุณต้องการให้ข้อตกลงของคุณมีสารบัญหรือไม่?": "ไม่",
      "q7: 2. ข้อเริ่มต้น (ENTRY CLAUSE)": "ได้",
      "q9: ข้อตกลงนี้จะลงนามในวันไหน?": "2025-08-14",
      "q10: จะมีวันที่แตกต่างจากวันที่ลงนามซึ่งข้อตกลงจะมีผลบังคับใช้หรือไม่?": "ไม่ใช่",
      "q11: ข้อตกลงผู้ถือหุ้นนี้ (ต่อไปจะเรียกว่า \\"ข้อตกลง\\" ให้พิมพ์ตกลง)": "ข้อตกลง",
      "q13: ตามกฎหมายบริษัทไทย บริษัทจำกัดจำเป็นต้องมีผู้ถือหุ้นอย่างน้อยสามคน ผู้ถือหุ้นคนแรกชื่ออะไร (เรียงตามลำดับอักษรของนามสกุล)?": "เร็ฏ",
      "q14: ผู้ถือหุ้นคนที่สองชื่ออะไร?": "เร็ก",
      "q15: ผู้ถือหุ้นคนที่สามชื่ออะไร?": "เร็ก",
      "q17: การทำข้อตกลงนี้จะมีบุคคลที่สามที่เป็นกลางเป็นพยานหรือไม่?": "ไม่ใช่",
      "q18: มีผู้ถือหุ้นอีกคนหรือไม่?": "ไม่ใช่",
      "q22: การทำข้อตกลงนี้จะมีทนายความโนตารี่ที่ได้รับการรับรองจากสภาทนายความเป็นพยานหรือไม่?": "ใช่",
      "q25: ข้อมูลของทนาย [ชื่อ, นามสกุล, เลขทะเบียน] ": "เร็ก ",
      "q26: บริษัทชื่ออะไร?": "เร็ก",
      "q27: วัตถุประสงค์ของบริษัทคืออะไร?": "เร็ก",
      "q28: บริษัทมีระยะเวลาการดำเนินงานหลังจากนั้นจะหยุดดำเนินงานหรือไม่?": "ไม่ใช่",
      "q30: บริษัทจะยุบเลิกเมื่อไหร่?": "2025-08-30"
    }
  }`;

  // ฟังก์ชันสำหรับแปลง JSON
  const parseJsonData = () => {
    try {
      let parsed = JSON.parse(jsonData || sampleData);
      
      // ถ้าไม่ใช่ array ให้แปลงเป็น array
      if (!Array.isArray(parsed)) {
        parsed = [parsed];
      }
      
      setParsedData(parsed);
      setError('');
    } catch (err) {
      setError('รูปแบบ JSON ไม่ถูกต้อง กรุณาตรวจสอบข้อมูล');
      setParsedData(null);
    }
  };

  // ฟังก์ชันสำหรับดึงค่าจากคำถาม
  const getAnswerByKey = (data, searchKey) => {
    if (!data || !Array.isArray(data)) return '';
    
    for (const item of data) {
      for (const answerKey in item) {
        const answers = item[answerKey];
        for (const question in answers) {
          if (question.includes(searchKey) || question.toLowerCase().includes(searchKey.toLowerCase())) {
            return answers[question];
          }
        }
      }
    }
    return '';
  };

  // ฟังก์ชันสำหรับสร้างเอกสาร
  const generateDocument = () => {
    if (!parsedData) return '';

    const companyName = getAnswerByKey(parsedData, 'บริษัทชื่ออะไร');
    const signDate = getAnswerByKey(parsedData, 'ข้อตกลงจะลงนามในวันไหน');
    const signLocation = getAnswerByKey(parsedData, 'ข้อตกลงจะลงนามที่ไหน');
    const shareholder1 = getAnswerByKey(parsedData, 'ผู้ถือหุ้นคนแรกชื่ออะไร');
    const shareholder2 = getAnswerByKey(parsedData, 'ผู้ถือหุ้นคนที่สองชื่ออะไร');
    const shareholder3 = getAnswerByKey(parsedData, 'ผู้ถือหุ้นคนที่สามชื่ออะไร');
    const companyPurpose = getAnswerByKey(parsedData, 'วัตถุประสงค์ของบริษัท');
    const lawyerInfo = getAnswerByKey(parsedData, '[ชื่อ, นามสกุล, เลขทะเบียน]');
    const agreementName = getAnswerByKey(parsedData, 'ข้อตกลงผู้ถือหุ้น');
    const companyyub = getAnswerByKey(parsedData, 'บริษัทจะยุบเลิกเมื่อไหร');

    return `
ข้อตกลงผู้ถือหุ้น
บริษัท ${companyName} จำกัด

วันที่: ${signDate}
สถานที่: ${signLocation}

${agreementName}นี้ทำขึ้นระหว่าง:
${shareholder1 ? `1. ${shareholder1}` : ''}
${shareholder2 ? `2. ${shareholder2}` : ''}  
${shareholder3 ? `3. ${shareholder3}` : ''}

วัตถุประสงค์: ${companyPurpose}

พยานโดยทนายความ: ${lawyerInfo}

ลงชื่อ ${companyName}
วันที่ ${signDate}
แล้วบริษัทจะระเบิดและยุบวันไหน ${companyyub}
`;
  };



  return (
    <>
      <div className="document-container">
      <h1 className="document-header">ระบบจัดการ Template เอกสาร</h1>
      
      <div className="document-section">
        <h2 className="section-title">1. นำเข้าข้อมูล JSON</h2>
        <textarea
          className="json-textarea"
          placeholder="วาง JSON ข้อมูลคำตอบที่นี่ หรือใช้ข้อมูลตัวอย่าง"
          value={jsonData}
          onChange={(e) => setJsonData(e.target.value)}
        />
        <div className="button-group">
          <button className="btn-primary" onClick={parseJsonData}>
            แปลงข้อมูล
          </button>
          <button 
            className="btn-secondary" 
            onClick={() => {
              setJsonData(sampleData);
              // Auto parse ข้อมูลตัวอย่าง
              setTimeout(() => {
                parseJsonData();
              }, 100);
            }}
          >
            ใช้ข้อมูลตัวอย่าง
          </button>
        </div>
        {error && <div className="error-message">{error}</div>}
      </div>

      {parsedData && (
        <>
          <div className="document-section">
            <h2 className="section-title">2. ข้อมูลที่แปลงแล้ว</h2>
            <table className="data-table">
              <thead>
                <tr>
                  <th className="table-header">คำถาม</th>
                  <th className="table-header">คำตอบ</th>
                </tr>
              </thead>
              <tbody>
                {parsedData.map((item, index) => (
                  Object.entries(item).map(([answerKey, answers]) => (
                    Object.entries(answers).map(([question, answer], qIndex) => (
                      <tr key={`${index}-${qIndex}`}>
                        <td className="table-cell">{question}</td>
                        <td className="table-cell">{answer}</td>
                      </tr>
                    ))
                  ))
                ))}
              </tbody>
            </table>
          </div>

          <div className="document-section">
            <h2 className="section-title">3. เอกสารที่สร้างขึ้น</h2>
            <div className="document-preview">
              {generateDocument()}
            </div>
            <div className="button-group">
              <button 
                className="btn-primary"
                onClick={() => {
                  const doc = generateDocument();
                  const blob = new Blob([doc], { type: 'text/plain;charset=utf-8' });
                  const url = URL.createObjectURL(blob);
                  const a = document.createElement('a');
                  a.href = url;
                  a.download = 'agreement.txt';
                  a.click();
                  URL.revokeObjectURL(url);
                }}
              >
                ดาวน์โหลดเอกสาร
              </button>
              <button 
                className="btn-secondary"
                onClick={() => {
                  navigator.clipboard.writeText(generateDocument());
                  alert('คัดลอกเอกสารเรียบร้อยแล้ว!');
                }}
              >
                คัดลอกเอกสาร
              </button>
            </div>
          </div>
        </>
      )}
    </div>
    </>
  );
};

export default DocumentTemplateManager;