import React, { useEffect, useState } from "react";
import { fetchQuestions } from "../api";
import './Editor.css'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronRight, faChevronLeft, faRotateLeft, faListCheck, faMinus, faPlus
  ,faFloppyDisk
 } from "@fortawesome/free-solid-svg-icons";


export default function QuestionFlow() {
  const [questions, setQuestions] = useState([]);
  const [currentId, setCurrentId] = useState(null);
  const [answers, setAnswers] = useState({});
  const [qHistory, setQHistory] = useState([]);
  const [isFinished, setIsFinished] = useState(false);
  const [showJson, setShowJson] = useState(false);




  useEffect(() => {
    fetchQuestions().then(data => {
      setQuestions(data.nodes || []);
      if (data.nodes && data.nodes.length > 0) {
        setCurrentId(data.nodes[0].id); 
      }
    });
  }, []);

  if (!questions.length) return <p>กำลังโหลดคำถาม...</p>;

  const currentQ = questions.find(q => q.id === currentId);
  if (!currentQ) return <p>ไม่พบคำถาม</p>;

  const handleAnswerChange = (e) => {
    setAnswers(prev => ({ ...prev, [currentQ.id]: e.target.value }));
  };

  const handleNext = () => {
    let nextId = null;

    if (currentQ.type === "dropdown" && currentQ.options) {
      const selectedOption = currentQ.options.find(opt => opt.label === answers[currentQ.id]);
      nextId = selectedOption ? selectedOption.next : null;
    } else {
      nextId = currentQ.next;
    }

    if (nextId) {
      setQHistory(prev => [...prev, currentQ.id]); 
      setCurrentId(nextId);
    } else {
      setIsFinished(true); 
    }
  };


  const handleBack = () => {
    if (qHistory.length === 0) return;
    const prevId = qHistory[qHistory.length - 1];
    setQHistory(qHistory.slice(0, -1));
    setCurrentId(prevId);
  };


  const handleRestart = () => {
    setAnswers({});
    setQHistory([]);
    setCurrentId(questions[0].id);
    setIsFinished(false);
  };

  const convertAnswersWithQuestions = () => {
  const result = {};

  questions.forEach((q) => {
    const answer = answers[q.id];
    if (answer !== undefined) {
      result[`${q.id}: ${q.question}`] = answer;
    }
  });

  return result;
};

  const handleSaveAnswers = async () => {
    const convertedAnswers = convertAnswersWithQuestions();  // ✅ ใช้อันนี้
    try {
      const response = await fetch("http://10.7.0.111:8000/save_answers", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(convertedAnswers),  // ✅ ใช้อันที่แปลงแล้ว
      });

      if (response.ok) {
        alert("✅ บันทึกข้อมูลสำเร็จแล้ว!");
      } else {
        alert("❌ ไม่สามารถบันทึกข้อมูลได้");
      }
    } catch (err) {
      console.error("Error saving answers:", err);
      alert("❌ เกิดข้อผิดพลาดในการบันทึก");
    }
  };

  async function getCurrentAnswerKey() {
    const res = await fetch("http://10.7.0.111:8000/get_all_answers");
    if (!res.ok) return null;

    const data = await res.json();
    const keys = Object.keys(data || {});
    return keys.length > 0 ? keys[keys.length - 1] : null;
  }

  
  if (isFinished) {
    return (
      <div className="summary-container">
        <h3><FontAwesomeIcon icon={faListCheck} /> สรุปคำตอบของคุณ</h3>
        <ul>
          {questions.map((q) =>
            answers[q.id] ? (
              <li key={q.id}>
                <strong>{q.question}</strong><br />
                <span style={{ color: "blue" }}>{answers[q.id]}</span>
              </li>
            ) : null
          )}
        </ul>
        <button onClick={handleRestart} className="btn-restart">
          <FontAwesomeIcon icon={faRotateLeft} /> เริ่มใหม่อีกครั้ง
        </button>
      <button onClick={() => setShowJson(!showJson)} className="btn-json">
        {showJson ? (
          <>
            <FontAwesomeIcon icon={faMinus} /> JSON
          </>
        ) : (
          <>
            <FontAwesomeIcon icon={faPlus} /> JSON
          </>
        )}
      </button>

        {showJson && (
          <pre className="json-box">
            {JSON.stringify(
              questions.reduce((acc, q) => {
                if (answers[q.id] !== undefined) {
                  acc[q.question] = answers[q.id];
                }
                return acc;
              }, {}),
              null,
              2
            )}
          </pre>
      )}
      <button onClick={handleSaveAnswers} className="btn-save">
         <FontAwesomeIcon icon={faFloppyDisk} /> บันทึก
      </button>
      <button
        className="btn-save"
        onClick={async () => {
          const answerKey = await getCurrentAnswerKey(); // ฟังก์ชันหา Answer ล่าสุด
          if (!answerKey) {
            alert("ยังไม่มีคำตอบที่บันทึกไว้");
            return;
          }

          const res = await fetch('"http://10.7.0.111:8000/generate_docx"', {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({ answer_key: answerKey }),
          });

          const result = await res.json();
          if (res.ok) {
            alert("📄 " + result.message);
          } else {
            alert("❌ สร้างเอกสารไม่สำเร็จ: " + result.error);
          }
        }}
      >
        สร้างเอกสาร
      </button>

      </div>
    );
  }

  return (
    <>
    <div className="container-q">
      <h3>คำถาม :</h3>
      <p>{currentQ.question}</p>

      {currentQ.type === "text" && (
        <input
          className="input-ui"
          type="text"
          value={answers[currentQ.id] || ""}
          onChange={handleAnswerChange}
          style={{ width: "100%", padding: 8, fontSize: 16 }}
        />
      )}

      {currentQ.type === "date" && (
        <input
          className="input-ui"
          type="date"
          value={answers[currentQ.id] || ""}
          onChange={handleAnswerChange}
          style={{ width: "100%", padding: 8, fontSize: 16 }}
        />
      )}

      {currentQ.type === "dropdown" && (
        <select
          className="input-ui"
          value={answers[currentQ.id] || ""}
          onChange={handleAnswerChange}
          style={{ width: "100%", padding: 8, fontSize: 16 }}
        >
          <option value="">-- กรุณาเลือก --</option>
          {currentQ.options.map((opt) => (
            <option key={opt.label} value={opt.label}>
              {opt.label}
            </option>
          ))}
        </select>
      )}

      <div style={{ marginTop: 20, display: "flex", gap: 10 , justifyContent: "space-between",}}>
        {/* 🔙 ปุ่มย้อนกลับ */}
        <button
          onClick={handleBack}
          disabled={qHistory.length === 0}
        >
          <FontAwesomeIcon icon={faChevronLeft} size="xl"/>
        </button>

        <button
          onClick={() => {
            const confirmRestart = window.confirm("คุณแน่ใจหรือไม่ว่าต้องการเริ่มคำถามใหม่?");
            if (confirmRestart) {
              handleRestart();
            }
          }}
        >
          <FontAwesomeIcon icon={faRotateLeft} size="xl" />
        </button>

        {/* ▶️ ปุ่มถัดไป */}
        <button
          onClick={handleNext}
          disabled={!answers[currentQ.id]}
        >
          <FontAwesomeIcon icon={faChevronRight} size="xl" />
        </button>
      </div>
    </div>
    </>
);

}
